import React from "react"
import "./Form.css"

const form = (props) => {
    let error
    if (props.message.length > 0) {
        error = "Error\r " + props.message;
    }

    return (
        <div className="ContactMe" id="4thSect">
            <form action="http://formspree.io/4shr4f@live.co.uk" method="post">
                <fieldset>
                    <h2 id="getInTouch">Contact Me</h2> 
                    <label for="email">Email</label>
                    <br/>  
                    <input type="email" name="_replyto" onChange={props.changeHandler}/>
                    <p>Please enter your message</p> 
                    <textarea name="body" onChange={props.changeHandler}></textarea>
                    <br/>
                    <p style={{color: "red"}}>{error}</p>
                    <button type="submit" disabled={props.error}>Send</button>
                </fieldset>
            </form> 
		</div> 
    )
}

export default form